<template>
	<div class="welcome">
		<div class="presentation-holder">
			<img class="logo-symbol" src="../assets/logo.svg" alt="Profil Logo">
			<h1 class="title">{{ msg }}</h1>
		</div>

		<div class="btn-holder">
			<form @submit.prevent>
				<social-button
					textInner="Entrar com o Linkedin"
					design="login"
				/>
				<main-input
					type="email"
					placeholder="Email"
					design="login"
					v-model="email"
				/>
				<main-input
					type="password"
					placeholder="Senha"
					design="login"
					v-model="password"
				/>
				<div class="login-holder">
					<router-link
						class="link"
						to="/cadastro"
					>
						Criar cadastro
					</router-link>
					<button
						class="submit-button"
						@click="onSubmit"
					>
						Entrar
					</button>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
import SocialButton from '../components/SocialButton';
import MainButton from '../components/MainButton';
import MainInput from '../components/MainInput';

export default {
	name: 'welcome',
	data() {
		return {
			msg: 'Explore diverasas profissões com quem trabalha na área',
			email: '',
			password: '',
		};
	},
	components: {
		'social-button': SocialButton,
		'main-button': MainButton,
		'main-input': MainInput,
	},
	methods: {
		onSubmit() {
			const formData = {
				email: this.email,
				password: this.password,
			};
			console.log(formData);
			this.$store.dispatch('login', {
				email: formData.email,
				password: formData.password,
			});
		},
	},
};
</script>

<style lang="stylus" scoped>
	@import '../assets/styles/_mixins'
	@import '../assets/styles/_colors'


	$header-height = 56px
	$border-radius = 10px

	::-webkit-input-placeholder
		color #fff

	.welcome
		linear_gradient(-140deg, lightness($pink, 60%), lightness($blue, 70%))
		padding 40px 20px
		box-sizing border-box
		display flex
		flex-direction column
		align-items center
		text-align center
		color #ffffff
		min-height 100vh
		justify-content space-between

		@media (min-width 768px)
			justify-content space-around

		.link
			color #ffffff

	.logo-symbol
		height 120px

	.title
		font-weight 300
		@media (min-width 768px)
			max-width 450px
			font-size 40px
			line-height 54px

		@media (max-width 768px)
			max-width 250px
			font-size 20px
			line-height 24px

	.login-holder
		display flex
		align-items center
		justify-content space-between

</style>



